import CardBrandService from '@/services/card/CardBrandService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CardBrandTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultCardBrandTableFilter: {
      type: String,
      default: null
    },
    defaultCardBrandTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      cardBrandTableData: [],
      cardBrandTableFilterTimer: null,
      cardBrandTableFilter: this.defaultCardBrandTableFilter,
      cardBrandTableOrderBy: this.defaultCardBrandTableOrderBy,
      cardBrandTableContainer: 'loading-container',
      cardBrandTableBusy: false,
      cardBrandTableTotalRows: 0,
      cardBrandTableCurrentPage: 1,
      cardBrandTablePerPage: 10
    }
  },
  computed: {
    cardBrandTableNoDataText: function () {
      return this.cardBrandTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    cardBrandTableTotalPages: function () {
      return Math.ceil(this.cardBrandTableTotalRows / this.cardBrandTablePerPage)
    },
    cardBrandTablePagingInfo: function () {
      return this.pagingInfo(this.cardBrandTableCurrentPage, this.cardBrandTablePerPage, this.cardBrandTableTotalRows)
    }
  },
  methods: {
    loadCardBrandTableData (loadingContainerId) {
      let self = this
      this.cardBrandTableBusy = true
      this.cardBrandTableContainer = loadingContainerId || this.cardBrandTableContainer
      this.startLoading(this.cardBrandTableContainer)
      CardBrandService.cardBrands({
        pageIndex: this.cardBrandTableCurrentPage - 1,
        pageSize: this.cardBrandTablePerPage,
        filter: this.cardBrandTableFilter,
        orderByField: this.cardBrandTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.cardBrandTableContainer)
        self.cardBrandTableBusy = false
        self.cardBrandTableTotalRows = response.data.totalRows
        self.cardBrandTableData = response.data.cardBrandList || []
      }).catch(function (error) {
        self.stopLoading(self.cardBrandTableContainer)
        self.cardBrandTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCardBrandTable () {
      this.loadCardBrandTableData()
    },
    filterCardBrandTable (filter) {
      let self = this
      if (this.cardBrandTableFilter !== filter) {
        clearTimeout(this.cardBrandTableFilterTimer)
        this.cardBrandTableFilterTimer = setTimeout(() => {
          self.cardBrandTableFilter = filter
          self.loadCardBrandTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.cardBrandTableFilterTimer)
      }
    },
    orderCardBrandTable (orderBy, active) {
      this.cardBrandTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCardBrandTableData()
    }
  },
  watch: {
    cardBrandTableCurrentPage: function () {
      this.refreshCardBrandTable()
    },
    cardBrandTablePerPage: function () {
      this.refreshCardBrandTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshCardBrandTable()
    }
  }
}

export default CardBrandTableMixin
